        border: 1px solid $border-color;
        z-index: 2;
        width: 70%;
        height: 300px;
        background: $white-color;
        position: absolute;
        top: 250%;
        left: 150%;
        overflow: scroll;
        display: none;
        transition: 1s;
        &-header{
          display: flex;
          padding: 1rem 0 1rem 0!important;
          border-bottom: 1px solid $border-color;
          &-closebtn{
            width: 30%;
            i{
              font-size: 1.2rem;
            }
          }
          &-title{
            padding: 0 !important;
            width: 70%;
            text-align: right;
          }
        }
        &-city{
          padding: 1rem 0 1rem 0 !important;
          text-align: right;
          border-bottom: 1px solid $light-color;
          label{
            padding-right: 1rem;
          }
        }
      }
    }
    &__menu{
      width: 80%;
      text-align: right;
      ul{
        display: flex;
        justify-content: end;
        list-style-type: none;
        li{
          position: relative;
          padding: 0 1.5rem;
          display:inline-block;
          border: 2px solid transparent;
          &::after{
            content: '';
            padding: 0.3rem;
            display: block;
            border-bottom: 2px solid red;
            transform: scaleX(0);
            transition: transform 250ms ease-in-out;
          }
          &:hover::after{
            transform: scaleX(1);
          }
          &.nav__menu-catrgory{
            padding-right: 0;
            &:hover{
              div.categories{
                display: block;
              }
            }
          }
          & > i{
            color: rgb(160, 160, 160);
          }
           .categories{
            text-align: right;
            position: absolute;
            top: 2.2rem;
            right: 0rem;
            background: $white-color;
